.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

#loginBox {
  font-family: 'Bitter', sans-serif;
  font-size: 1.5rem;
  width: 45vw;
  margin-top: 2rem;
  padding: 2.5rem;
  border: 1px solid #6610f2;
  float: left;
  box-sizing: border-box;
  height: 90vh;
}

#leftColumn {
  width: 17vw;
  float:left;
  margin:3em;
}

.centerDiv
{
  width: 45vw; 
  margin: 0 auto; 
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
